import './App.css';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';
import { Bar } from 'react-chartjs-2';
import React, { useState, useEffect } from 'react';

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
);

function App() {

  const [chartData, setChartData] = useState({
    datasets: [],
  });

  const [chartOptions, setChartOptions] = useState({});

  useEffect(() => {
    setChartData({
      labels: ["John", "Kevin", "George", "Micheal", "Oreo"],
      datasets: [
        {
          label: "who let's the dogs out",
          data: [12, 55, 34, 120, 720],
          borderColor: "rgb(153,62,235, 1)",
          backgroundColor: "rgba(53, 262, 235, 0.4)",

        },
      ],
    });
    setChartOptions({
      responsive: true,
      plugins: {
        legend: {
          position: "top"
        },
        title: {
          display: true,
          text: "who let the dogs out",
        }
      }
    })
  })
  return (
    <div className="App">
      <Bar options={chartOptions} data={chartData} />
    </div>
  );
}

export default App;
